<template>

	<div class="row gx-0 step_progress_alt">
		<div class="col-lg-8 order-2 order-lg-1">
			<div class="step_progress_alt_steps" data-cy="progress_bar">

				<div
					:class="step_info_class(1)"
					class="step_info"
				>
					<div class="step_number">1.</div>
					<div class="step_title">Pojištěné osoby</div>
				</div>
				<div
					:class="step_info_class(2)"
					class="step_info"
				>
					<div class="step_number">2.</div>
					<div class="step_title">Parametry pojištění</div>
				</div>
				<div
					:class="step_info_class(3)"
					class="step_info"
				>
					<div class="step_number">3.</div>
					<div class="step_title">Porovnání nabídek</div>
				</div>

			</div>
		</div>
		<div class="col-lg-4 order-1 order-lg-2">
			<div class="step_progress_alt_pagination pb-3 pb-lg-0" data-cy="navigation">

				<button
					v-if="show_back"
					@click="prev"
					class="btn btn-outline-primary btn-left btn-m me-1"
				>
					Zpět
				</button>
				<button
					@click="clear"
					class="btn btn-outline-primary btn-m mx-1"
				>
					Smazat údaje
				</button>
				<button
					@click="next"
					class="btn btn-primary btn-right btn-m ms-1"
				>
					Další
				</button>

			</div>
		</div>
	</div>

</template>

<script>
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
			show_back: Boolean,
		},
		components: {
		},
		emits: ['next', 'prev'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			next() {
				this.$emit('next');
			},
			prev() {
				this.$emit('prev');
			},
			clear() {
				STATE_modals().open_it('Cleaning_session');
			},
			step_info_class(int)
			{
				if (int < this.step) {
					return 'completed';
				}
				else if (int === this.step) {
					return 'current';
				}
				else {
					return '';
				}
			},
		},
		computed: {
			step()
			{
				return STATE_core().current_step();
			},
		},
		watch: {
		},
	}
</script>
